ks-comment {
    display: block;
}

ks-comment article {
    padding: 30px;
}

@media (min-width: 1200px) {
    ks-comment article {
        padding: 30px 40px;
    }
}

ks-comment header {
    display: flex;
}

ks-comment header > div {
    width: 100%;
}

ks-comment header .title {
    margin: 0;
}

ks-comment header .info {
    font-size: .875rem;
    line-height: 1.4;
    color: #707070;
}

ks-comment ks-icon { 
    margin-right: 20px;
}

ks-comment p {
    margin-bottom: 0px;
}