import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-comment',
    styleUrl: 'comment.css'
})
export class Comment {
    @Prop() author: string;
    @Prop() when: string;

	render() {
		return <article>
            <header>
                <ks-icon name="user" size={2}></ks-icon>
                <div>
                    <h3 class="title">{this.author}</h3>
                    <div class="info">{this.when}</div>
                </div>
            </header>
            <slot/>
        </article>;
	}
}
